<template>
  <div class="wrapper">
    <v-md-preview :text="base"></v-md-preview>
  </div>
  <div class="test-wrapper">
    <fx-skeleton type="1" rows="3"></fx-skeleton>
  </div>
  <div class="test-wrapper">
    <fx-skeleton type="2" rows="5"></fx-skeleton>
  </div>
  <div class="wrapper">
    <v-md-preview :text="baseHtml"></v-md-preview>
  </div>
  <div class="wrapper">
    <v-md-preview :text="article"></v-md-preview>
  </div>
  <div class="test-wrapper flex">
    <fx-skeleton type="3" rows="3" width="300"></fx-skeleton>
    <fx-skeleton type="3" rows="5" width="500"></fx-skeleton>
  </div>
  <div class="wrapper">
    <v-md-preview :text="articleHtml"></v-md-preview>
  </div>
  <div class="test-wrapper">
    <fx-skeleton type="2" rows="5" active></fx-skeleton>
  </div>
  <div class="wrapper">
    <v-md-preview :text="active"></v-md-preview>
  </div>
  <div class="wrapper">
    <v-md-preview :text="desc"></v-md-preview>
  </div>
  <div class="bottom"></div>
</template>

<script>
import {ref} from "vue";

export default {
  name: "aafxSkeleton",
  setup() {
    const base = ref('# Skeleton 骨架屏\n' +
        '在需要等待加载内容的位置提供一个占位图形组合。\n' +
        '## 何时使用\n' +
        '1. 网络较慢，需要长时间等待加载处理的情况下。\n' +
        '2. 图文信息内容较多的列表/卡片中。\n' +
        '3. 只在第一次加载数据的时候使用。\n' +
        '## 基础用法\n' +
        '基础的骨架效果。使用type 属性来选择不同的骨架效果，rows 属性定义骨架屏段落数量。')

    const baseHtml = ref('### 代码\n' +
        '``` html\n' +
        '<fx-skeleton type="1" rows="3"></fx-skeleton>\n' +
        '<fx-skeleton type="2" rows="5"></fx-skeleton>\n' +
        '```')

    const article = ref('## 面板骨架屏\n' +
        '当 type 属性设置为3时，显示面板骨架屏，可用 width 属性定义面板宽度')

    const articleHtml = ref('### 代码\n' +
        '``` html\n' +
        ' <fx-skeleton type="3" rows="3" width="300"></fx-skeleton>\n' +
        ' <fx-skeleton type="3" rows="5" width="500"></fx-skeleton>\n' +
        '```\n' +
        '\n' +
        '## 动画效果\n' +
        'active 属性定义是否显示加载动画，所有的 fx-skeleton 的子节点将显示动画。')

    const active = ref('### 代码\n' +
        '``` html\n' +
        '<fx-skeleton type="2" rows="5" active></fx-skeleton>\n' +
        '```')
    const desc = ref('## Skeleton 属性\n' +
        '|属性|说明|类型|可选值|\n' +
        '|-|-|-|-|\n' +
        '|type|设置骨架屏显示效果|string/number|1/2/3|\n' +
        '|rows|骨架屏段落数量|string/number|-|\n' +
        '|width|当type为3时，设置面板宽度|number|-|\n' +
        '|active|是否展示动画效果|boolean|true/false|')
    return {
      base, baseHtml, article, articleHtml, active,desc
    }
  }
}
</script>

